@import url(https://fonts.googleapis.com/css2?family=Roboto&display=swap);
.container{height:100vh;max-width:1200px;margin:100px auto;padding:0 30px}html,body,ul,h1,h2,h3,p,span,button{margin:0;padding:0}a{text-decoration:none}ul{list-style:none}button{border:none;background:none;outline:none}figure{margin:0}*{box-sizing:border-box}html{font-family:'Roboto', sans-serif}.files-table{display:-webkit-flex;display:flex;-webkit-align-items:center;align-items:center;-webkit-justify-content:center;justify-content:center}.files-table__button{cursor:pointer;height:100%}.files-table__cell-content{display:-webkit-flex;display:flex;-webkit-align-items:center;align-items:center;grid-gap:10px;gap:10px}.files-table__cell-image{display:-webkit-flex;display:flex;-webkit-align-items:center;align-items:center;-webkit-justify-content:center;justify-content:center;width:32px;height:32px}.breadcrumbs{margin-bottom:30px}.breadcrumbs__button{cursor:pointer;font-size:1rem}.toolbar{display:-webkit-flex;display:flex;grid-gap:20px;gap:20px;min-height:40px;margin-bottom:50px}.toolbar__input-file{display:none}.toolbar__label{display:-webkit-flex;display:flex;-webkit-align-items:center;align-items:center;-webkit-justify-content:center;justify-content:center;width:100%;height:100%;cursor:pointer}.modal__form{display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;grid-gap:10px;gap:10px}

